import './smtp.scss';
import React, { useState } from 'react';
 
const Smtp = (params) => {
  const [username, setUsername] = useState('');
  const [password, setPassword] = useState('');
  const [host, setHost] = useState('');
  const [modalVisible, setModalVisible] = useState(params.modalVisible);
 
  const closeModal = async () => {
        
    setModalVisible(false);
    params.handleItemClick("");

};
 
  const handleUsernameChange = (event) => {
    setUsername(event.target.value);
    console.log(username);
  };
 
  const handlePasswordChange = (event) => {
    setPassword(event.target.value);
    console.log(password);
  };
 
  const handleHostChange = (event) => {
    setHost(event.target.value);
    console.log(host);
  };
 
  const handleSubmit = () => {
    closeModal();
   };
 
  return (
    <>

        {  modalVisible ?
    <div className={`smtp-container d-flex flex-column justify-content-center grid gap-3 pt-4 pb-4 px-4  `}>
      <h2 className='head d-flex justify-content-center'>SMTP</h2>
      <div className="input-container pt-2">
      <div className="label">
      <label htmlFor="username">Username </label>
      </div>
      <div className="input ">
      <input
          type="text"
          id="username"
          value={username}
          onChange={handleUsernameChange}
        />
      </div>
        
      </div>
      <div className="input-container pt-2">
      <div className="label">
      <label htmlFor="password">Password </label>
      </div>
        <div className="input ">
          <input
            type="password"
            id="password"
            value={password}
            onChange={handlePasswordChange}
          />
        </div> 
      </div>
      <div className="input-container pt-2">
      <div className="label">
      <label htmlFor="host">Host </label>
      </div>
      <div className="input ">
      <input
          type="text"
          id="host"
          value={host}
          onChange={handleHostChange}
        />
      </div>
      </div>
        <div className='buttons m-0 pt-3'>
        <button className="close-btn px-2 py-2 rounded" onClick={closeModal}>Close</button>
        <button className="submit btn" onClick={handleSubmit}>Submit</button>
        
        </div>
      
    </div>:<></>
        }

</>
  );
};
 
export default Smtp;