import React, { createContext, useState } from 'react';
import CobRunningBatch from '../CobStage_RunningBatch/cobRunningBatch.jsx';
import CobProgressChart from '../CobProgressChart/cobProgressChart.jsx';
import Table from '../CobRuntime_CobCompletiontime/cobRuntime_CobCompletiontime.jsx';
import CobRunningTime from '../Timer/CobRunningTime.jsx';
import './home.scss';

export const Context=createContext()

const Homecomponent = () => {

    const [selectedCOB, setSelectedCOB] = useState('');
    const [timerFlag, setTimerFlag]=useState(0);
    const [timerRunning, setTimerRunning] = useState(false);
  
    return (
        <div className="row">
            <div className='col-md-12 d-flex grid gap-3 justify-content-between'>
                <div className='progressChart col-md-6'>
                    <CobProgressChart />
                </div>
                <div className='RunningBatch col-md-6 '>
                    <CobRunningBatch/>
                </div>
            </div>
                <div className='col-md-12 d-flex grid gap-3 justify-content-between' style={{ paddingTop: '1rem' }}>
                    <div className='table col-md-12'>
                    <Table />
                    </div>
                    
                </div>   
        </div>
          
        );
};

export default Homecomponent;