import React, { useState, useEffect, useContext } from 'react';
import CobElapsedTime from './DisplayPausedTime';
import './CobRunningTime.scss';
import { Context } from "../../App";
const CobRunningTime = () => {
  const [startTime, setStartTime] = useState(0);
  const [elapsedTime, setElapsedTime] = useState(0);
  const [isRunning, setIsRunning] = useState(false);
  const[progressPercent, setProgressPercent]=useState(0);
 
  const [jsonData, setJsonData] = useState({
    "header": {
      "audit": {
        "T24_time": 4656,
        "responseParse_time": 8,
        "requestParse_time": 1188
      },
      "page_start": 1,
      "page_token": "202204195230244035.01,99",
      "total_size": 7,
      "page_size": 99,
      "status": "success"
    },
    "body": []
  });
 
 
  const value=useContext(Context);
console.log(value.progressPercent);
let percent=parseInt(value.progressPercent);
if(percent===100){
  handleReset();
}
  useEffect(() => {
    // Simulate fetching data from an API and setting jsonData
    setTimeout(async() => {
      try {
        const response = await fetch('http://127.0.0.1:81/accountViewCont/api/v1.0.0/party/cobProgress');
        if (!response.ok) {
          throw new Error('Failed to fetch data');
        }
        // const jsonData = await response.json();
        setJsonData(await response.json());
      } catch (error) {
        console.error('Error fetching data:', error);
      }
    }, 2000); // Simulating a delay of 2 seconds
  }, []);
 
  // Load start time from localStorage on component mount
  useEffect(() => {
    const storedStartTime = localStorage.getItem('startTime');
    if (storedStartTime) {
      const storedElapsedTime = Math.floor((Date.now() - parseInt(storedStartTime)) / 1000);
      setStartTime(parseInt(storedStartTime));
      setElapsedTime(storedElapsedTime);
      setIsRunning(true); // Start the timer if it was running before
    }
  }, []);
 
  useEffect(() => {
    // Start the timer if jsonData has progress values and the timer is not already running
    if (jsonData.body.some(item => item.Progress !== "0 0 0" && item.Progress !== "") && !isRunning) {
      handleStartStop();
    }
  }, [jsonData]);
 
  useEffect(() => {
    let intervalId;
    if (isRunning) {
      intervalId = setInterval(() => {
        setElapsedTime(prevElapsedTime => prevElapsedTime + 1);
      }, 1000);
    } else {
      clearInterval(intervalId);
    }
    return () => clearInterval(intervalId);
  }, [isRunning]);
 
  const apiAuth = () => {
    const extractedData = jsonData.body
      .filter(item => item.Progress !== "0 0 0" && item.Progress !== "")
      .map(item => ({
        name: item.Service, // Use service data directly as names
        value: item.Progress ? "1" : null // Return "1" if item.Progress is not null
      }));
 
    if (extractedData.length > 0) {
      console.log("extracted data: " + true);
      handleStartStop();
    }
  };
 
  const handleStartStop = () => {
    setIsRunning(prevIsRunning => !prevIsRunning);
    if (!isRunning) {
      const now = Date.now();
      localStorage.setItem('startTime', now.toString()); // Store the current time as start time
      setStartTime(now);
    }
  };
 
  const handleReset = () => {
    localStorage.removeItem('startTime'); // Remove the start time from localStorage
    localStorage.setItem('pausedTime', elapsedTime.toString());
    setStartTime(0);
    setElapsedTime(0);
    setIsRunning(false);
  };
 
 
  const formatTime = (timeInSeconds) => {
    const hours = Math.floor(timeInSeconds / 3600);
    const minutes = Math.floor((timeInSeconds % 3600) / 60);
    const seconds = Math.floor(timeInSeconds % 60);
 
    return `${String(hours).padStart(2, '0')} : ${String(minutes).padStart(2, '0')} : ${String(seconds).padStart(2, '0')}`;
  };
 
  return (
    <div className='container-fluid'>
      <div className="col-md-12 d-flex justify-content-end">
      <div className="col-md-11 clock-container">
      <h2 className='header'>Elapsed Time</h2>
      <div className='time '><h2>{formatTime(elapsedTime)}</h2></div>
      {/* <button  className='btn btn-success reset mt-3' onClick={handleReset}>Reset</button> */}
      <CobElapsedTime />
      </div>
      
      </div>
      
    </div>
  );
};
 
export default CobRunningTime;