import React, { useState } from 'react';
import './popup.scss';
import SelectBox from '../Selectbox/Cobselectbox';
import { toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
 
const Modal = () => {
  const [selectedValue, setSelectedValue] = useState(null);
  const [modalVisible, setModalVisible] = useState(true);
 
  const closeModal = () => {
    setModalVisible(false);
  };
 
  const handleSelectChange = (value) => {
    console.log(value);
    setSelectedValue(value);
  };
 
  const startCob = async () => {
    try {
      const response = await fetch('http://localhost:5000/startCob', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json'
        },
        body: JSON.stringify({
          "body": {
            cobName: selectedValue
          }
        }),
      });
      if (!response.ok) {
        throw new Error('Failed to edit the record');
      }
      else {
        closeModal();
        toast.success(selectedValue + " Started successfully");
      }
    } catch (error) {
      console.error('Error updating data:', error);
      toast.error('Failed to start COB');
    }
  };
 
  const handleOkClick = () => {
    if (selectedValue !== null) {
      startCob();
    } else {
      toast.error("Please select a value");
    }
  };
 
  return (
    <div className={`modal-box ${modalVisible ? 'visible' : 'hidden'}`}>
      <div className="selectbox">
        <SelectBox onChange={handleSelectChange} />
        </div>
      <div className="buttons">
      <button className="close-btn px-2 py-2 rounded" onClick={closeModal}>Close</button>
        <button className="btn submit" onClick={handleOkClick}>Ok</button>
       
      </div>
    </div>
  );
};
 
export default Modal;